body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** graph components **/
.header {
    margin-bottom: 0px;
    text-align: left;
}

.sorry {
    font-size: 12px;
    margin-top: 40vh;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    -webkit-align-items: center;
            align-items: center;
}

.key {
    -webkit-align-items: left;
            align-items: left;
    width: 20%
}

.info {
    width: 15px;
    float: right;
    padding-left: auto;
}

.infoDiv {
    margin-right: 0;
    margin-left: auto;
    width: 0;
    margin-bottom: auto;
    height: 20px;
}

.info:hover + .hiddenInfo {
    display: block;
}

.mobileHeading {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.heading {
    margin-top: 8vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
}

.hiddenInfo {
    display: none;
    font-size: 12px;
    width: 20em;
    margin-top: 18px;
    margin-right: 0;
    margin-left: -200px;
    text-align: start;
    border: 1px solid grey;
    padding: 5px;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5); 
    font-family: Helvetica, Arial;
    font-style: normal;
    font-weight: 100;
    border: 1px solid grey;
    border-radius: 0.5em;
    font-size: x-small;
    position: inherit;
    z-index: 1;
}

.infoHeading {
    margin-bottom: 2px;
}

.bigCanvas { 
    margin-top: 50px;
}

@media only screen and (max-width: 700px) {
    .hiddenInfo {
        max-width: 110px;
        margin-left: -110px;
    }
}

@media only screen and (max-width: 400px) {
    .heading {
        margin-top: 11vh;
    }

    .header {
        font-size: 16px;
    }

    .infoDiv {
        display: none
    }
}

@media only screen and (max-width: 280px) {
    .header {
        font-size: 12px;
    }

    .hiddenInfo {
        font-size: 8px;
    }
}

.songInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 180px;
}

.smallSongInfo {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    width: 100px;
}

.mobile {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    margin: auto;
    width: 16rem;
    margin-top: 2vh;
}

.container {
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.name { 
    margin: 5px;
    text-align: left;
    font-size: small;
    margin-bottom: 0;
    margin-right: 10px;
}

.artist { 
    margin: 5px;
    margin-top: 0;
    text-align: left;
    font-size: xx-small; 
    margin-right: 10px;
    margin-bottom: 0;
}

.left {
    text-align: left;
    width: 40%;
    font-size: 18px;
    margin-left: 10%;
}

.right {
    text-align: left;
    width: 40%;
    font-size: 18px;
}

.list {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    margin-left: 0%;
    margin-bottom: 30px;
}

.top10 {
    margin-top: 10px;
    margin-bottom: 6px;
    margin-left: 10%;
    text-align: left;
    font-size: 24px;
} 

.topzz {
    -webkit-align-items: center;
            align-items: center;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content; 
}

.listOfSongs {
    width: 400%;
}

.songCover {
    height: 50px;
    margin: 10px;
}

@media only screen and (max-width: 600px) {
    .left {
        font-size: 14px;
    }

    .right {
        font-size: 14px;
    }

    .top10 {
        font-size: 18px;
    }
}

@media only screen and (max-width: 400px) {
    .left {
        font-size: 3vw;
    }

    .right {
        font-size: 3vw;
    }

    .top10 {
        font-size: 4vw;
    }
}

.genreComponent {
    font-size: xx-small;
    background-color: #000;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border: 1px solid grey;
    border-radius: 0.5em;
    color: #fff;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
    margin-top: 2px;
}

.parent { 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    text-align: left;
    margin-right: 10px;
    max-width: 180px;
    margin-bottom: 5px;
}
.header {
    font-size: 24px;
    margin-bottom: 20px;
}

.songs {
    font-size: 14px;
    text-align: left;
}

.top {
    border-left: 1px solid grey;
    padding-left: 10px;
    margin-left: 25px;
    margin-right: 35px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.margins {
    background-color: #000;
    color: #fff;
    background: rgb(204, 204, 204); /* Fallback for older browsers without RGBA-support */
    background: rgba(52, 51, 51, 0.5);   
    border-radius: 0.5em;
    margin-left: 130px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 10%;
}

.load {
    font-size: 20px;
    padding-top: 20px;
    margin-left: 10px;
}

@media only screen and (max-width: 900px) {
    .margins {
        display: none;
    }
  }
.App {
  text-align: center;
}

.App-header {
  background-color: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

html, body {
  height: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  color: #eee;
  font-family: aktiv-grotesk, sans-serif;  
  font-size: 3vmin;
  font-style: normal;
  font-weight: 200;
}

.hidden {
  display: none;
}

/** Buttons **/

.btn {
  background-color: rgba(52, 51, 51, 0.5);   
  border-radius: 2em;
  border: 1px solid grey;
  color: white;
  font-size: 14px;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
  margin-top: 20px;
}

.btn:hover {
  background: #1DB954;
  /* background: linear-gradient(90deg, rgba(255,241,0,0.9) 0%, rgba(29,253,203,0.9) 50%, rgba(252,0,206,0.9) 100%); */
  border: none;
  color: #000;
}

.btn--login {
  margin: 0 auto;
}

/** Front Page **/

.logo {
  margin-top: 10px;
  width: 10%;
  height: 10%;
  padding-bottom: 130px;
}

.main {
  display: inline;
  margin-top: 30vh;
}

.hi {
  margin-top: 20px;
  font-size: 10px;
  color: grey;
}

@media only screen and (max-width: 500px) {
  .title {
    font-size: 40px;
  }

  .logo {
    margin-top: 10px;
    width: 20%;
    height: 20%;
  }

}

@media only screen and (max-width: 250px) {
  .btn {
    font-size: 10px;
  }

  .logo {
    margin-top: 10px;
    width: 25%;
    height: 25%;
  }

  .hi {
    width: 150px; 
    margin-left: auto;
    margin-right: auto;
  }
}

@media only screen and (max-height: 500px) {
  .main {
    margin-top: 12vh;
  }
}
